import React ,{Component}from 'react';
import { Card,Accordion ,Button} from 'react-bootstrap';
import axios from 'axios';
var i=0;

class All_states  extends Component{

  constructor(){

    super();
    this.state={
      stateData:{}
    }
  }
   state={

    name:{}
   }
    componentDidMount(){
    
        axios.get("https://api.covid19india.org/state_district_wise.json").then(res =>{
         var persons = res.data;
         this.setState({stateData:persons});
         
         
         
      })
      }

    
    render(){
    
           
             var key=this.state.stateData;
             var sts=Object.keys(key);
             


               return(  
               
                
                    
               <div className="pt-5">
                 <h1 className="p-5">STATEWISE DATA IN INDIA</h1>
            
               
    <Accordion defaultActiveKey="0">

  

    {
        
        sts.map((val,ky)=>{
         var v=this.state.stateData[val].districtData;
         var dstkey=Object.keys(v);
        
         

return(
          

  
  <Card>
    <Card.Header>
      <Accordion.Toggle as={Button} variant="link" eventKey={ky}>
        {val}
      </Accordion.Toggle>
    </Card.Header>
    <Accordion.Collapse eventKey={ky}>
      <Card.Body>
        <table className="table table-bordered table-active table-responsive">
            <thead>
                    <tr>
                      <td>Districts</td>
                      <td>Confirmed</td>
                      <td>Active</td>
                      <td>Recovered</td>
                      <td>Deaths</td>
                    </tr>

            </thead>
            <tbody>
              {

                   dstkey.map((itm,ind)=>{
                      
                      console.log(itm.active);
                    return(
                  <tr>
                     <td>{itm}</td>
                    <td>{itm.confirmed}</td>
                     <td>{itm.active}</td>
                     <td>{itm.recovered}</td>
                     <td>{itm.recovered}</td>




                  </tr>
                    )
      })

              }

            </tbody>




        </table>




      </Card.Body>
    </Accordion.Collapse>
  </Card>

        )
        





        })



    }
     
  
</Accordion>
             
         </div>         
                 
               )
    }     
                
               
                 

}
export default All_states;